  margin-bottom: 10vh;
}

.bg-img{

  background-image:url('https://images.unsplash.com/photo-1505691723518-36a5ac3be353?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Nnx8aG9tZXxlbnwwfHwwfHw%3D&auto=format&fit=crop&w=700&q=60');
  min-height: 90vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}

.container{
    position: absolute;
    margin:0;
    width: 100%;
    
} 

.txt{
  text-align: left;
  font-size: 40;
  color:white;

}

.txt-2{
  text-align: left;
  color:#ff4500

}

