  margin-bottom: 2px;

}

.home-tab {
  padding: 5vh;
  padding-right: 7vw;
  border-radius: 60px 0;
  width: 60vw;
  margin: auto;
  display: flex;
  flex-direction: column;
  margin-top: 18vh;
  background-color: #4b2004a1;
}

.home-tab button, .nav-icon button{
  background-color: orangered;
}

.nav-color {
  background-color: #4b2004a1;
  height: 10vh;
  color: white;

}

.nav-icon .icon {
  margin-right: px;
